/** @format */

import {useLazyQuery} from '@apollo/client';
import {Box} from '@mui/material';
import React from 'react';
import {formatNumberNoDecimals, formatNumberTwoDecimals,} from '../../../functions/numberFormats';
import {camelizeKeys, toCamelCase} from '../../../functions/stringFormats';
import {GET_SUBMITTED_APPLICATION_STATS, GET_UNSUBMITTED_APPLICATION_DATA,} from '../../../gql/queries/applications';
import ApplicationSelection from '../../selectors/ApplicationSelection';
import ApplicationSummary from './ApplicationSummary';

function ApplicationEnquiry(props) {
    const [dataLoaded, setDataLoaded] = React.useState(false);
    const [appHeaderData, setAppHeaderData] = React.useState({});
    const [appAreaData, setAppAreaData] = React.useState([]);
    const [appOrderData, setAppOrderData] = React.useState([]);
    const [appLocationData, setAppLocationData] = React.useState([]);
    const [appItemData, setAppItemData] = React.useState([]);
    const [appImageData, setAppImageData] = React.useState();

    const [getSubmittedApplicationData] = useLazyQuery(
        GET_SUBMITTED_APPLICATION_STATS,
        {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
            onCompleted: (data) => {
                setAppHeaderData(
                    camelizeKeys(
                        data.submittedApplicationByApplicationId.applicationHeader[0],
                    ),
                );
                setAppAreaData(
                    data.submittedApplicationByApplicationId.areas.map((item) =>
                        camelizeKeys(item),
                    ),
                );
                setAppOrderData(
                    data.submittedApplicationByApplicationId.orderheaders.map((item) => {
                        const newKeys = camelizeKeys(item);
                        return {
                            ...newKeys,
                            id: newKeys.orderId,
                            remainingValue: formatNumberTwoDecimals(
                                newKeys.orderValue - newKeys.cumulativeApplicationValue,
                            ),
                            imageCount:
                            data.submittedApplicationByApplicationId.images?.filter(
                                (obj) => obj.orderheader_id === newKeys.orderId,
                            ).length,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                newKeys.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberTwoDecimals(
                                newKeys.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberTwoDecimals(
                                newKeys.prevCumulativeApplicationValue,
                            ),
                            orderValue: formatNumberTwoDecimals(newKeys.orderValue),
                        };
                    }),
                );
                setAppLocationData(
                    data.submittedApplicationByApplicationId.sitelocations.map((item) => {
                        const newKeys = camelizeKeys(item);
                        return {
                            ...newKeys,
                            remainingValue: formatNumberTwoDecimals(
                                newKeys.orderValue - newKeys.cumulativeApplicationValue,
                            ),
                            imageCount:
                            data.submittedApplicationByApplicationId.images?.filter(
                                (obj) => obj.location_id === newKeys.id,
                            ).length,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                newKeys.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberTwoDecimals(
                                newKeys.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberTwoDecimals(
                                newKeys.prevCumulativeApplicationValue,
                            ),
                            orderValue: formatNumberTwoDecimals(newKeys.orderValue),
                        };
                    }),
                );

                setAppItemData(
                    data.submittedApplicationByApplicationId.orderdetails.map((item) => {
                        const newKeys = camelizeKeys(item);
                        return {
                            ...newKeys,
                            remainingValue: formatNumberTwoDecimals(
                                newKeys.orderValue - newKeys.cumulativeApplicationValue,
                            ),
                            imageCount:
                            data.submittedApplicationByApplicationId.images?.filter(
                                (obj) => obj.location_id === newKeys.id,
                            ).length,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                newKeys.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberTwoDecimals(
                                newKeys.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberTwoDecimals(
                                newKeys.prevCumulativeApplicationValue,
                            ),
                            orderValue: formatNumberTwoDecimals(newKeys.orderValue),
                        };
                    }),
                );
                const img = data.submittedApplicationByApplicationId.images;
                setAppImageData(img ? img.map((item) => toCamelCase(item)) : []);

                setDataLoaded(true);
            },
        },
    );

    const [getUnSubmittedApplicationData] = useLazyQuery(
        GET_UNSUBMITTED_APPLICATION_DATA,
        {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
            onCompleted: (data) => {
                setAppHeaderData({
                    ...data.query.applicationSummaryWithCumulativeValue,
                    cumulativeApplicationValue: formatNumberTwoDecimals(
                        data.query.applicationSummaryWithCumulativeValue
                            .cumulativeApplicationValue,
                    ),
                    thisApplicationValue: formatNumberNoDecimals(
                        data.query.applicationSummaryWithCumulativeValue
                            .thisApplicationValue,
                    ),
                    prevCumulativeApplicationValue: formatNumberNoDecimals(
                        data.query.applicationSummaryWithCumulativeValue
                            .prevCumulativeApplicationValue,
                    ),
                });
                setAppAreaData(
                    data.query.applicationSummaryAreaWithCumulativeValues.nodes.map(
                        (item) => ({
                            ...item,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                item.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberNoDecimals(
                                item.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberNoDecimals(
                                item.prevCumulativeApplicationValue,
                            ),
                        }),
                    ),
                );
                setAppOrderData(
                    data.query.applicationSummaryOrderheaderWithCumulativeValues.nodes.map(
                        (item) => ({
                            ...item,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                item.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberTwoDecimals(
                                item.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberTwoDecimals(
                                item.prevCumulativeApplicationValue,
                            ),
                            orderValue: formatNumberTwoDecimals(item.orderValue),
                            remainingValue: formatNumberTwoDecimals(
                                item.orderValue - item.cumulativeApplicationValue,
                            ),
                            id: item.orderId,
                            imageCount: data.query.applicationImageDetails.nodes.filter(
                                (obj) => obj.orderheaderId === item.orderId,
                            ).length,
                        }),
                    ),
                );
                setAppLocationData(
                    data.query.applicationSummarySitelocationWithCumulativeValues.nodes.map(
                        (item) => ({
                            ...item,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                item.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberTwoDecimals(
                                item.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberTwoDecimals(
                                item.prevCumulativeApplicationValue,
                            ),
                            orderValue: formatNumberTwoDecimals(item.orderValue),
                            remainingValue: formatNumberTwoDecimals(
                                item.orderValue - item.cumulativeApplicationValue,
                            ),
                            imageCount: data.query.applicationImageDetails.nodes.filter(
                                (obj) => obj.locationId === item.id,
                            ).length,
                        }),
                    ),
                );
                setAppItemData(
                    data.query.applicationSummaryOrderdetailWithCumulativeValues.nodes.map(
                        (item) => ({
                            ...item,
                            cumulativeApplicationValue: formatNumberTwoDecimals(
                                item.cumulativeApplicationValue,
                            ),
                            thisApplicationValue: formatNumberTwoDecimals(
                                item.thisApplicationValue,
                            ),
                            prevCumulativeApplicationValue: formatNumberTwoDecimals(
                                item.prevCumulativeApplicationValue,
                            ),
                            orderValue: formatNumberTwoDecimals(item.orderValue),
                            remainingValue: formatNumberTwoDecimals(
                                item.orderValue - item.cumulativeApplicationValue,
                            ),
                        }),
                    ),
                );
                setAppImageData(data.query.applicationImageDetails.nodes);
                setDataLoaded(true);
            },
        },
    );

    return (
        <Box m={2}>
            <ApplicationSelection
                setDataLoaded={setDataLoaded}
                getSubmittedApplicationData={getSubmittedApplicationData}
                getUnSubmittedApplicationData={getUnSubmittedApplicationData}
            />
            {dataLoaded && (
                <Box>
                    <ApplicationSummary
                        headerData={appHeaderData}
                        areaData={appAreaData}
                        orderData={appOrderData}
                        locationData={appLocationData}
                        itemData={appItemData}
                        imageData={appImageData}
                    />
                </Box>
            )}
        </Box>
    );
}

export default ApplicationEnquiry;
