/** @format */

import {Box} from '@mui/material';
import React from 'react';
import {formatNumberNoDecimals} from '../../../functions/numberFormats';

function ApplicationStatistics(props) {
    return (
        <Box
            //   height={'78vh'}
            sx={{
                border: 2,
                background: '#d0cece',
                p: 2,
                m: 1,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'flex-start',
                flexGrow: 1,
                flexShrink: 1,
            }}>
            <Box component='h3' m={0} mb={2} sx={{textDecoration: 'underline'}}>
                APPLICATION SUMMARY
            </Box>
            <Box sx={{border: 2, background: 'white'}} mb={2}>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>PREVIOUS CUMULATIVE APPLICATION VALUE:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.prevCumulativeApplicationValue)}
                    </Box>
                </Box>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>THIS APPLICATION VALUE:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.thisApplicationValue)}
                    </Box>
                </Box>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>CUMULATIVE APPLICATION VALUE:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.cumulativeApplicationValue)}
                    </Box>
                </Box>
            </Box>
            <Box sx={{border: 2, background: 'white'}}>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>AREAS INCLUDED:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.areaCount)}
                    </Box>
                </Box>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>ORDERS INCLUDED:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.orderCount)}
                    </Box>
                </Box>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>LOCATIONS INCLUDED:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.locationCount)}
                    </Box>
                </Box>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>ITEMS INCLUDED:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.itemCount)}
                    </Box>
                </Box>
                <Box
                    sx={{display: 'flex', justifyContent: 'space-between'}}
                    pl={2}
                    pr={2}>
                    <Box component='h4'>IMAGES INCLUDED:</Box>
                    <Box component='h4'>
                        {formatNumberNoDecimals(props.data.imageCount)}
                    </Box>
                </Box>
            </Box>
        </Box>
    );
}

export default ApplicationStatistics;
