/** @format */

import {Box, Button, Grid} from '@mui/material';
import Typography from '@mui/material/Typography';
import React from 'react';
import {useModal} from 'react-modal-hook';
import {formatNumberNoDecimals} from '../../../functions/numberFormats';
import StyledModal from '../../ui-components/modals/StyledModal';
import ApplicationAreaDetail from './ApplicationAreaDetail';

const ProjectsModal = ({
                           areaData,
                           orderData,
                           locationData,
                           itemData,
                           imageData,
                           data,
                       }) => {
    const [showProjectsModal, hideProjectsModal] = useModal(() => {
        return (
            <StyledModal preventScroll>
                <Grid container={true}>
                    <Grid container mb={3}>
                        <Button
                            color='secondary'
                            onClick={hideProjectsModal}
                            sx={{width: 300}}
                            fullWidth={false}>
                            CLOSE
                        </Button>
                    </Grid>

                    <Grid container>
                        <ApplicationAreaDetail
                            data={data}
                            orderData={orderData}
                            locationData={locationData}
                            itemData={itemData}
                            imageData={imageData}
                        />
                    </Grid>
                </Grid>
            </StyledModal>
        );
    }, [data, areaData, orderData, locationData, itemData, imageData]);

    return (
        <Button sx={{p: '2px 25px'}} color='action' onClick={showProjectsModal}>
            SHOW DETAIL
        </Button>
    );
};

function ApplicationAreas(props) {
    const {areaData, imageData} = props.data;
  
    return (
        <Box
            sx={{
                border: 2,
                background: '#d0cece',
                p: 2,
                m: 1,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'flex-start',
                flexGrow: 1,
                flexShrink: 1,
            }}>
            <Box component='h3' m={0} mb={2} sx={{textDecoration: 'underline'}}>
                AREA SUMMARIES
            </Box>

            {areaData.map((item) => (
                <Box
                    key={item.areaId}
                    sx={{border: 2, background: 'white'}}
                    mb={2}
                    p={1}>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography fontSize={18} fontWeight={'bold'}>
                            AREA:
                        </Typography>
                        <Typography fontSize={18} fontWeight={'bold'}>
                            {item.areaDescription}
                        </Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography>PREVIOUS CUMULATIVE APPLICATION VALUE:</Typography>
                        <Typography>
                            {formatNumberNoDecimals(item.prevCumulativeApplicationValue)}
                        </Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography fontWeight={'bold'}>THIS APPLICATION VALUE:</Typography>
                        <Typography fontWeight={'bold'}>
                            {formatNumberNoDecimals(item.thisApplicationValue)}
                        </Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography>CUMULATIVE APPLICATION VALUE:</Typography>
                        <Typography>
                            {formatNumberNoDecimals(item.cumulativeApplicationValue)}
                        </Typography>
                    </Box>{' '}
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography>ORDERS INCLUDED:</Typography>
                        <Typography>{formatNumberNoDecimals(item.orderCount)}</Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography>LOCATIONS INCLUDED:</Typography>
                        <Typography>
                            {formatNumberNoDecimals(item.locationCount)}
                        </Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography>ITEMS INCLUDED:</Typography>
                        <Typography>{formatNumberNoDecimals(item.itemCount)}</Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'space-between'}} m={1}>
                        <Typography>IMAGES INCLUDED:</Typography>
                        <Typography>
                            {formatNumberNoDecimals(
                                imageData?.filter((obj) => obj.areaId === item.areaId).length,
                            )}
                        </Typography>
                    </Box>
                    <Box sx={{display: 'flex', justifyContent: 'center'}}>
                        <ProjectsModal
                            areaData={item}
                            orderData={props.data.orderData.filter(
                                (obj) => obj.areaId === item.areaId,
                            )}
                            locationData={props.data.locationData.filter(
                                (obj) => obj.areaId === item.areaId,
                            )}
                            itemData={props.data.itemData.filter(
                                (obj) => obj.areaId === item.areaId,
                            )}
                            imageData={props.data.imageData.filter(
                                (obj) => obj.areaId === item.areaId,
                            )}
                            data={props.data}
                        />
                    </Box>
                </Box>
            ))}
        </Box>
    );
}

export default ApplicationAreas;
