/** @format */

import {gql} from '@apollo/client';

export const GET_ALL_APPLICATIONS = gql`
    query GetAllApplications {
        applicationSummaryWithCumulativeValues {
            nodes {
                applicationCurrent
                applicationDate
                applicationNumber
                applicationOpen
                applicationReference
                applicationSubmitted
                thisApplicationValue
                dateSubmitted
                id
                imageCount
                itemCount
                locationCount
                orderCount
                submissionReference
                cumulativeApplicationValue
                areaCount
            }
        }
    }
`;

export const GET_APPLICATION_SUBMISSION_DATA = gql`
    query GetApplicationSubmissionData($id: Int!) {
        submittedApplicationByApplicationId(applicationId: $id) {
            applicationHeader
            applicationId
            areas
            images
            orderdetails
            orderheaders
            sitelocations
            worksheets
        }
    }
`;

export const GET_APPLICATION_VALUES_FOR_SINGLE_ORDERHEADER = gql`
    query GetApplicationValuesForSingleOrderheader($id: Int!) {
        worksheetWithValuesCalculateds(
            condition: { orderheaderId: $id, applied: true }
        ) {
            groupedAggregates(groupBy: APPLICATION_NUMBER) {
                keys
                sum {
                    valueComplete
                }
            }
        }
    }
`;

export const GET_SINGLE_APPLICATION_DATA = gql`
    query GetSingleApplicationData($id: Int!) {
        applicationWithValueById(id: $id) {
            applicationCurrent
            applicationDate
            applicationNumber
            applicationOpen
            applicationReference
            applicationSubmitted
            applicationValue
            areaCount
            dateSubmitted
            id
            imageCount
            itemCount
            locationCount
            orderCount
            worksheetCount
        }
    }
`;

export const GET_SUBMITTED_APPLICATION_STATS = gql`
    query GetSubmittedApplicationStats($id: Int!) {
        submittedApplicationByApplicationId(applicationId: $id) {
            applicationHeader
            applicationId
            areas
            images
            orderdetails
            orderheaders
            sitelocations
            worksheets
        }
    }
`;

export const GET_APPLICATION_AREA_DATA = gql`
    query GetApplicationAreaData($id: Int!) {
        applicationSummaryAreaWithCumulativeValues(
            condition: { applicationId: $id }
            orderBy: AREA_ID_ASC
        ) {
            nodes {
                areaDescription
                areaId
                cumulativeApplicationValue
                itemCount
                locationCount
                orderCount
                prevCumulativeApplicationValue
                thisApplicationValue
                applicationId
                applicationNumber
                application {
                    id
                }
            }
        }
    }
`;

export const GET_APPLICATION_AREA_ORDERHEADERS = gql`
    query GetApplicationAreaOrderheaders($applicationId: Int!, $areaId: Int!) {
        applicationSummaryOrderheaderWithCumulativeValues(
            condition: { applicationId: $applicationId, areaId: $areaId }
        ) {
            nodes {
                orderId
                orderNumber
                projectTitle
                orderValue
                prevCumulativeApplicationValue
                thisApplicationValue
                locationCount
                itemCount
                cumulativeApplicationValue
            }
        }
    }
`;

export const GET_APPLICATION_ORDERHEADER_LOCATIONS = gql`
    query GetApplicationOrderheaderLocations($orderId: Int, $applicationId: Int) {
        applicationSummarySitelocationWithCumulativeValues(
            condition: { applicationId: $applicationId, orderId: $orderId }
        ) {
            nodes {
                imageCount
                itemCount
                worksheetCount
                orderValue
                prevCumulativeApplicationValue
                projectTitle
                reference
                thisApplicationValue
                worksheetReference
            }
        }
    }
`;

export const GET_UNSUBMITTED_APPLICATION_DATA = gql`
    query GetUnsubmittedApplicationData($id: Int!, $areaId: Int) {
        query {
            applicationSummaryWithCumulativeValue(id: $id) {
                applicationDate
                applicationNumber
                applicationReference
                applicationSubmitted
                areaCount
                cumulativeApplicationValue
                id
                imageCount
                itemCount
                locationCount
                orderCount
                prevCumulativeApplicationValue
                submissionReference
                thisApplicationValue
                worksheetCount
            }
            applicationSummaryAreaWithCumulativeValues(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    applicationId
                    areaId
                    areaDescription
                    cumulativeApplicationValue
                    itemCount
                    locationCount
                    orderCount
                    prevCumulativeApplicationValue
                    thisApplicationValue
                    applicationNumber
                    worksheetCount
                }
            }
            applicationSummaryOrderheaderWithCumulativeValues(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    orderNumber
                    projectTitle
                    orderValue
                    prevCumulativeApplicationValue
                    thisApplicationValue
                    cumulativeApplicationValue
                    locationCount
                    itemCount
                    worksheetCount
                    cumulativeClaimCount
                    applicationId
                    areaDescription
                    areaId
                    orderId
                }
            }
            applicationSummarySitelocationWithCumulativeValues(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    worksheetReference
                    reference
                    orderValue
                    prevCumulativeApplicationValue
                    thisApplicationValue
                    cumulativeApplicationValue
                    itemCount
                    worksheetCount
                    cumulativeClaimCount
                    applicationId
                    applicationNumber
                    areaId
                    id
                    orderId
                    orderNumber
                    projectTitle
                }
            }
            applicationSummaryOrderdetailWithCumulativeValues(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    itemNumber
                    qtyApplied
                    activityCode
                    activityDescription
                    orderValue
                    prevCumulativeApplicationValue
                    thisApplicationValue
                    cumulativeApplicationValue
                    worksheetCount
                    applicationId
                    areaId
                    id
                    locationId
                    orderId
                    reference
                    worksheetReference
                }
            }
            applicationImageDetails(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    applicationId
                    areaId
                    exif
                    exifDate
                    exifGps
                    headerImageFile
                    imageId
                    imageTypeId
                    locationId
                    longName
                    orderheaderId
                    reference
                    shortName
                    worksheetReference
                    dateTakenManual
                }
            }
            worksheetWithValuesCalculateds(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    qtyComplete
                    dateComplete
                    orderdetailId
                    valueComplete
                    supervisorName
                    itemNumber
                    activityCode
                    activityDescription
                }
            }
            applicationImageDetails(
                condition: { applicationId: $id, areaId: $areaId }
            ) {
                nodes {
                    locationId
                    imageId
                    headerImageFile
                    dateTakenManual
                    shortName
                    longName
                    exifDate
                    orderheaderId
                    areaId
                }
            }
        }
    }
`;

export const GET_ALL_APPLICATION_BASICS_TABLE = gql`
    query GetAllApplicationBasics {
        applicationSummaryWithCumulativeValues {
            nodes {
                appNumber: applicationNumber
                appDate: applicationDate
                appValue: thisApplicationValue
                cumAppValue: cumulativeApplicationValue
                areas: areaCount
                projects: orderCount
                locations: locationCount
                items: itemCount
                worksheets: worksheetCount
                images: imageCount
                id
            }
        }
    }
`;
