/** @format */

import {DateTime} from 'luxon';

export const formatDate = (date) => {
    const converted = DateTime.fromISO(date);
    const isValid = DateTime.isDateTime(converted);
    if (isValid)
        return converted.setLocale('en-GB').toLocaleString(DateTime.DATE_SHORT);
};

export const fixKeys = (fn) => (obj) =>
    Object.fromEntries(
        Object.entries(obj).map(([k, v]) => [
            fn(k),
            Array.isArray(v)
                ? v.map(fixKeys(fn))
                : typeof v == 'object'
                    ? fixKeys(fn)(v)
                    : v,
        ]),
    );

export function getInitials(name) {
    return name
        .match(/(\b\S)?/g)
        .join('')
        .toUpperCase();
}

export const camelCase = (s) => s.replace(/_(.)/g, (s, c) => c.toUpperCase());

export const snakeCase = (s) => s.replace(/_(.)/g, (s, c) => c.toLowerCase());

export const camelizeKeys = fixKeys(camelCase);

export const SnakeCaseToWords = fixKeys(snakeCase);

export const camelCaseToText = (str) => str.replace(/^[a-z]|[A-Z]/g, (c, i) => (i ? " " : "") + c.toUpperCase());

export function toCamelCase(obj) {
    let rtn = obj;
    if (!rtn) {
        return rtn;
    } else if (typeof obj === 'object') {
        if (obj instanceof Array) {
            rtn = obj.map(toCamelCase);
        } else {
            rtn = {};
            for (let key in obj) {
                if (obj.hasOwnProperty(key)) {
                    const newKey = key.replace(/(_\w)/g, (k) => k[1].toUpperCase());
                    rtn[newKey] = toCamelCase(obj[key]);
                }
            }
        }
    }
    return rtn;
}

export const truncateString = (str, len) => {
    if (str.length > len) {
        if (len <= 3) {
            return str.slice(0, len - 3) + "...";
        } else {
            return str.slice(0, len) + "...";
        }
        ;
    } else {
        return str;
    }
};

export function capitalizeFirstLetterOfEachWord(sentence) {
    return sentence.split(" ").map((word) => {
        return word[0].toUpperCase() + word.substring(1).toLowerCase();
    }).join(" ");
}
