/** @format */

import {Box, Button, Typography} from '@mui/material';
import {DataGrid} from '@mui/x-data-grid';
import React from 'react';
import {useModal} from 'react-modal-hook';
import StyledModal from '../../ui-components/modals/StyledModal';
import ImageViewer from "../../ui-components/image-viewer/ImageViewer";

const LocationItems = ({itemData, params}) => {
    const columns = [
        {
            field: 'itemNumber',
            headerName: 'Item',

            flex: 1,
        },
        {
            field: 'activityCode',
            headerName: 'Code',
            //	minWidth: 80,
            flex: 1,
        },
        {
            field: 'activityDescription',
            headerName: 'Description',
            //	minWidth: 80,
            flex: 2,
        },
        {
            field: 'qtyApplied',
            headerName: 'Qty Applied',
            type: 'number',
            //	minWidth: 80,
            flex: 1,
        },
        {
            field: 'prevCumulativeApplicationValue',
            headerName: 'Prev Cum App Value',
            type: 'number',
            //	minWidth: 100,
            flex: 1,
            //cellClassName: 'grid-bold-font',
        },
        {
            field: 'thisApplicationValue',
            headerName: 'This App Value',
            type: 'number',
            //	minWidth: 100,
            flex: 1,
            cellClassName: 'grid-bold-font',
        },
        {
            field: 'cumulativeApplicationValue',
            headerName: 'Cum App Value',
            type: 'number',
            //	minWidth: 100,
            flex: 1,
            //	cellClassName: 'grid-bold-font',
        },
        {
            field: 'orderValue',
            headerName: 'Order Value',
            type: 'number',
            //	minWidth: 100,
            flex: 1,
            //	cellClassName: 'grid-bold-font',
        },
        {
            field: 'remainingValue',
            headerName: 'Remaining Value',
            type: 'number',
            //	minWidth: 100,
            flex: 1,
            cellClassName: 'grid-bold-font',
        },
    ];
    return (
        <Box sx={{
            backgroundColor: '#e6e6e6',
            p: 2,
            border: 'solid',
            borderWidth: 2,
            borderRadius: 2,
            mt: 3,
            width: '50%',
            mr: 2
        }}>
            <Typography
                sx={{
                    textAlign: 'center',
                    textDecoration: 'underline',
                    color: 'primary',
                    fontStyle: 'italic',
                    fontSize: 20,
                    fontWeight: 'bold',
                    mb: 2,
                }}>
                Item Listing For Location {params.row.reference} &nbsp;
                Application Number {params.row.applicationNumber}
            </Typography>
            <DataGrid
                autoHeight={true}
                columns={columns}
                rows={itemData}
                // pageSize={25}
                //  rowsPerPageOptions={[25]}
                //  hideFooterPagination={false}
                density='compact'
                //   hideFooter={false}
                checkboxSelection={false}
                disableSelectionOnClick={true}
                showColumnRightBorder
                showCellRightBorder
                sx={{
                    borderWidth: 1,
                    borderRadius: 1,
                    borderColor: 'black',
                    p: 1,
                    '& .MuiDataGrid-columnHeaders': {
                        color: 'white',
                        backgroundColor: '#4e667e',
                    },
                }}
            />
        </Box>
    );
};

const ItemsByLocation = ({itemData, imageData, params}) => {

    const images = {imageDetails: {nodes: imageData}}
    const [showItemsModal, hideItemsModal] = useModal(() => {
        console.log(params, imageData, itemData, images);
        return (
            <StyledModal preventScroll>
                <Button
                    color='secondary'
                    onClick={hideItemsModal}
                    sx={{width: 300}}
                >
                    CLOSE
                </Button>
                <Box display={'flex'} justifyContent={'space-between'}>
                    <LocationItems itemData={itemData} imageData={imageData} params={params}/>
                    <Box mt={3} width={'50%'} sx={{
                        backgroundColor: '#e6e6e6',
                        p: 2,
                        border: 'solid',
                        borderWidth: 2,
                        borderRadius: 2,
                    }}>
                        <Typography
                            sx={{
                                textAlign: 'center',
                                textDecoration: 'underline',
                                color: 'primary',
                                fontStyle: 'italic',
                                fontSize: 20,
                                fontWeight: 'bold',
                                mb: 1,
                            }}>
                            Images For Location {params.row.reference} &nbsp;
                            Application Number {params.row.applicationNumber}
                        </Typography>

                        <ImageViewer data={images}/>


                    </Box>
                </Box>


                {/* <Grid container={true}>
                    <Grid item xs={12} mb={3}>

                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item >

                    </Grid>
                    <Grid item

                    >

                    </Grid>
                </Grid>
*/}

            </StyledModal>
        );
    }, [itemData, imageData, params]);
    return (
        <Button
            fullWidth
            color='navigation'
            sx={{border: 0, borderRadius: 0, m: 'auto'}}
            onClick={showItemsModal}>
            VIEW ITEMS
        </Button>
    );
};

export default ItemsByLocation;
