/** @format */

import {Box} from '@mui/material';
import {DataGrid} from '@mui/x-data-grid';
import React from 'react';

const columns = [
    {field: 'orderNumber', headerName: 'Order Number', minWidth: 80, flex: 1},
    {
        field: 'projectTitle',
        headerName: 'Project Title',
        minWidth: 100,
        flex: 1,
        //	disableClickEventBubbling: true,
    },
    {
        field: 'locationCount',
        headerName: 'Locations',
        type: 'number',
        minWidth: 100,
        flex: 1,
    },
    {
        field: 'imageCount',
        headerName: 'Images',
        type: 'number',
        minWidth: 80,
        flex: 1,
    },
    {
        field: 'cumulativeClaimCount',
        headerName: 'Claim No',
        type: 'number',
        minWidth: 80,
        flex: 1,
    },
    {
        field: 'prevCumulativeApplicationValue',
        headerName: 'Prev Cum App Value',
        type: 'number',
        minWidth: 100,
        flex: 1,
        //	cellClassName: 'grid-bold-font',
    },
    {
        field: 'thisApplicationValue',
        headerName: 'This App Value',
        type: 'number',
        minWidth: 100,
        flex: 1,
        cellClassName: 'grid-bold-font',
    },
    {
        field: 'cumulativeApplicationValue',
        headerName: 'Cum App Value',
        type: 'number',
        minWidth: 100,
        flex: 1,
        //	cellClassName: 'grid-bold-font',
    },
    {
        field: 'orderValue',
        headerName: 'Order Value',
        type: 'number',
        minWidth: 100,
        flex: 1,
        //	cellClassName: 'grid-bold-font',
    },
    {
        field: 'remainingValue',
        headerName: 'Remaining Value',
        type: 'number',
        minWidth: 100,
        flex: 1,
        cellClassName: 'grid-bold-font',
    },
];

function AreaProjects(props) {
    return (
        <Box
            sx={{
                backgroundColor: '#e6e6e6',
                p: 2,
                border: 'solid',
                borderWidth: 2,
                borderRadius: 2,
            }}>
            <Box
                display={'flex'}
                sx={{
                    justifyContent: 'center',
                    textDecoration: 'underline',
                    color: 'primary',
                    fontStyle: 'italic',
                    fontSize: 20,
                    fontWeight: 'bold',
                    mb: 1,
                }}>
                Project Listing For Application Number{' '}
                {props.orderData[0].applicationNumber}{' '}
                {props.orderData[0].areaDescription} Area
            </Box>
            <DataGrid
                autoHeight={true}
                columns={columns}
                rows={props.orderData}
                density='compact'
                checkboxSelection={false}
                showColumnRightBorder
                showCellRightBorder
                onCe
                hideFooter={true}
                onRowClick={(params) => props.handleOrderSelected(params)}
                sx={{
                    borderWidth: 1,
                    borderRadius: 1,
                    borderColor: 'black',
                    p: 1,
                    '& .MuiDataGrid-columnHeaders': {
                        color: 'white',
                        backgroundColor: '#4e667e',
                    },
                    '& .MuiDataGrid-row': {
                        '&:hover': {
                            cursor: 'pointer',
                        },
                    },
                }}
            />
        </Box>
    );
}

export default AreaProjects;
