/** @format */

import {Box} from '@mui/material';
import {DataGrid} from '@mui/x-data-grid';
import React from 'react';
import ItemsByLocation from './ItemsByLocation';

function LocationsByOrder({locationData, itemData, imageData}) {
    console.log(itemData, imageData);
    const columns = [
        {
            field: 'worksheetReference',
            headerName: 'Worksheet',
            minWidth: 100,
            flex: 1,
        },
        {
            field: 'reference',
            headerName: 'Reference',
            minWidth: 100,
            flex: 1,
        },
        {
            field: 'itemCount',
            headerName: 'Items',
            type: 'number',
            minWidth: 80,
            flex: 1,
        },
        {
            field: 'imageCount',
            headerName: 'Images',
            type: 'number',
            minWidth: 80,
            flex: 1,
        },
        {
            field: 'cumulativeClaimCount',
            headerName: 'Claim No',
            type: 'number',
            minWidth: 80,
            flex: 1,
        },
        {
            field: 'prevCumulativeApplicationValue',
            headerName: 'Prev Cum App Value',
            type: 'number',
            minWidth: 100,
            flex: 1,
            //cellClassName: 'grid-bold-font',
        },
        {
            field: 'thisApplicationValue',
            headerName: 'This App Value',
            type: 'number',
            minWidth: 100,
            flex: 1,
            cellClassName: 'grid-bold-font',
        },
        {
            field: 'cumulativeApplicationValue',
            headerName: 'Cum App Value',
            type: 'number',
            minWidth: 100,
            flex: 1,
            //	cellClassName: 'grid-bold-font',
        },
        {
            field: 'orderValue',
            headerName: 'Order Value',
            type: 'number',
            minWidth: 100,
            flex: 1,
            //	cellClassName: 'grid-bold-font',
        },
        {
            field: 'remainingValue',
            headerName: 'Remaining Value',
            type: 'number',
            minWidth: 100,
            flex: 1,
            cellClassName: 'grid-bold-font',
        },

        {
            field: 'items',
            headerName: 'Action',
            headerAlign: 'center',
            sortable: false,
            cellClassName: 'remove-cell-padding',
            disableClickEventBubbling: true,
            flex: 1,
            renderCell: (params) => {
                return (
                    <ItemsByLocation
                        params={params}
                        imageData={imageData.filter((obj) => obj.locationId === params.id)}
                        itemData={itemData.filter((obj) => obj.locationId === params.id)}
                    />
                );
            },
        },
    ];
    return (
        <Box
            sx={{
                backgroundColor: '#e6e6e6',
                p: 2,
                border: 'solid',
                borderWidth: 2,
                borderRadius: 2,
            }}>
            <Box
                display={'flex'}
                sx={{
                    justifyContent: 'center',
                    textDecoration: 'underline',
                    color: 'primary',
                    fontStyle: 'italic',
                    fontSize: 20,
                    fontWeight: 'bold',
                    mb: 1,
                }}>
                Location Listing For Project {locationData[0].projectTitle} &nbsp;
                Application Number {locationData[0].applicationNumber}
            </Box>
            <DataGrid
                autoHeight={true}
                columns={columns}
                rows={locationData}
                density='compact'
                hideFooter={true}
                checkboxSelection={false}
                disableSelectionOnClick={true}
                showColumnRightBorder
                showCellRightBorder
                hideFooterPagination
                sx={{
                    borderWidth: 1,
                    borderRadius: 1,
                    borderColor: 'black',
                    p: 1,
                    '& .MuiDataGrid-columnHeaders': {
                        color: 'white',
                        backgroundColor: '#4e667e',
                    },
                }}
            />
        </Box>
    );
}

export default LocationsByOrder;
