/** @format */

import {gql, useQuery} from '@apollo/client';
import {Autocomplete, TextField} from '@mui/material';
import React from 'react';

const GET_ALL_APPLICATION_BASICS = gql`
    query GetAllApplicationBasics {
        applications {
            nodes {
                id
                applicationNumber
                applicationReference
                applicationSubmitted
            }
        }
    }
`;

function ApplicationSelection(props) {
    const {other} = props;
    const [applicationOptions, setApplicationOptions] = React.useState([]);

    const {loading} = useQuery(GET_ALL_APPLICATION_BASICS, {
        fetchPolicy: 'network-only',
        onCompleted: (data) => {
            setApplicationOptions(data.applications.nodes);
        },
    });

    function handleGetData(data) {
        if (data.applicationSubmitted) {
            props.getSubmittedApplicationData({
                variables: {id: data.id},
            });
        } else {
            props.getUnSubmittedApplicationData({
                variables: {id: data.id},
            });
        }
    }

    return (
        <Autocomplete
            {...other}
            blurOnSelect={true}
            loading={loading}
            size='small'
            disableClearable
            getOptionLabel={(option) =>
                `APPLICATION NUMBER:  ${option.applicationNumber}`
            }
            options={applicationOptions}
            onChange={(_event, newValue) => handleGetData(newValue)}
            isOptionEqualToValue={(option, value) => option.id === value.id}
            renderInput={(params) => (
                <TextField
                    {...params}
                    variant='filled'
                    label='Application Number'
                    InputProps={{
                        ...params.InputProps,
                    }}
                />
            )}
        />
    );
}

export default ApplicationSelection;
