/** @format */

import {Box} from '@mui/material';
import React from 'react';
import {formatDate} from '../../../functions/stringFormats';
import ApplicationAreas from './ApplicationAreas';
import ApplicationStatistics from './ApplicationStatistics';

function ApplicationSummary(props) {
    return (
        <Box
            sx={{background: '#e6e6e6'}}
            p={1}
            mt={3}
            display={'flex'}
            flexDirection={'column'}>
            <Box
                display='flex'
                justifyContent='space-between'
                m={1}
                p={1}
                sx={{
                    border: 2,
                    fontWeight: 'bold',
                    textTransform: 'uppercase',
                    background: '#d0cece',
                    fontSize: 18,
                }}>
                <div>
                    APPLICATION NUMBER:&nbsp; {props.headerData.applicationNumber}
                </div>
                <div>
                    APPLICATION REFERENCE:&nbsp; {props.headerData.applicationReference}
                </div>
                <div>
                    APPLICATION DATE: &nbsp;{' '}
                    {formatDate(props.headerData.applicationDate)}
                </div>
                <div>
                    APPLICATION SUBMITTED: &nbsp;{' '}
                    {props.headerData.applicationSubmitted ? 'TRUE' : 'FALSE'}
                </div>
            </Box>
            <Box display={'flex'}>
                <ApplicationStatistics data={props.headerData}/>
                <ApplicationAreas data={props}/>
            </Box>
        </Box>
    );
}

export default ApplicationSummary;
