/** @format */

import {Grid} from '@mui/material';
import React from 'react';
import AreaProjects from './AreaProjects';
import LocationsByOrder from './LocationsByOrder';

function ApplicationAreaDetail(props) {
    console.log(props)
    const {orderData, locationData, itemData, imageData} = props;

    const [selectedOrderLocations, setSelectedOrderLocations] = React.useState(
        [],
    );
    const [selectedOrderItems, setSelectedOrderItems] = React.useState([]);
    const [selectedOrderImages, setSelectedOrderImages] = React.useState([]);

    const handleOrderSelected = (row) => {
        console.log(row, locationData);
        setSelectedOrderLocations(
            locationData.filter((obj) => obj.orderId === row.id),
        );
        setSelectedOrderItems(itemData.filter((obj) => obj.orderId === row.id));
        setSelectedOrderImages(
            imageData.filter((obj) => obj.orderheaderId === row.id),
        );
    };

    return (
        <Grid container rowSpacing={5}>
            <Grid item xs={12}>
                <AreaProjects
                    handleOrderSelected={handleOrderSelected}
                    orderData={orderData}
                />
            </Grid>

            {selectedOrderLocations.length > 0 && (
                <Grid item xs={12}>
                    <LocationsByOrder
                        locationData={selectedOrderLocations}
                        itemData={selectedOrderItems}
                        imageData={selectedOrderImages}
                    />
                </Grid>
            )}
        </Grid>
    );
}

export default ApplicationAreaDetail;
